import { ChangeDetectorRef, Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Data, Params, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Subscription, combineLatest } from 'rxjs';
import { FieldDefinition, FixedDataSource, IFieldOptions, MetaDataType, Tab } from 'shared-code-lib';
import { Log } from '../../blocks/core/log';
import { Blurb, MarketingBlurb } from '../../blocks/models/marketing-blurb';
import { ErrorsService } from '../../blocks/services/errors.service';
import { HydraApiService } from '../../blocks/services/hydra-api.service';
import { ApiResult, Control } from '../../blocks/services/models';
import { SpinnerStateService } from '../../blocks/services/spinner-state.service';

@Component({
  selector: 'app-admin-marketing-blurb',
  templateUrl: './admin-marketing-blurb.component.html',
  styleUrls: ['./admin-marketing-blurb.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AdminMarketingBlurbComponent implements OnInit, OnDestroy {
  rightControls: Array<Control> = [];

  routeParams: Params;
  routeData: Data;

  saveButton: Control;
  cancelButton: Control;
  MarketingBlurb: MarketingBlurb;

  fieldName: FieldDefinition;
  fieldRelease: FieldDefinition;
  tabs = [];

  activeTab = 'blurbs';

  subscriptions = new Subscription();

  constructor(
    private translate: TranslateService,
    private router: Router,
    private route: ActivatedRoute,
    private hydraApi: HydraApiService,
    private toastr: ToastrService,
    private errorsService: ErrorsService,
    public spinner: SpinnerStateService,
    private cdr: ChangeDetectorRef
  ) {}

  ngOnInit() {
    this.subscriptions.add(
      combineLatest([this.route.params, this.route.data]).subscribe(([params, data]) => {
        this.routeParams = params;
        this.routeData = data;

        if (this.routeData.type == 'create') {
          this.MarketingBlurb = new MarketingBlurb({
            id: '',
            name: '',
            state: 0,
            release: '',
            brandLinks: [],
            blurbs: [new Blurb()],
            dateCreatedUTC: new Date(),
            dateModifiedUTC: new Date(),
          });
        } else {
          this.loadItems();
        }
      })
    );

    this.createButtons();
    this.createFields();
    this.createTabs();
  }

  private createTabs() {
    this.tabs.push(new Tab('blurbs', this.translate.instant('admin-marketing-blurb.blurbs')));
    this.tabs.push(new Tab('brands', this.translate.instant('admin-marketing-blurb.brands')));
  }

  private createButtons = () => {
    this.saveButton = new Control(this.onSave, 'Save', 'btn btn-primary btn-minwidth', '', '', 'btn', false);
    this.cancelButton = new Control(this.onCancel, 'Cancel', 'btn btn-light btn-minwidth', '', '', 'btn', false);
    this.rightControls.push(this.saveButton);
    this.rightControls.push(this.cancelButton);

    this.saveButton.setTranslation(this.translate.instant('Common.Save'));
    this.cancelButton.setTranslation(this.translate.instant('Common.Cancel'));
  };

  private createFields = () => {
    this.fieldName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-marketing-blurb.name'),
      placeholder: this.translate.instant('admin-marketing-blurb.name-placeholder'),
      required: true,
    });

    this.fieldRelease = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('admin-marketing-blurb.release'),
      placeholder: this.translate.instant('admin-marketing-blurb.release-placeholder'),
      required: true,
      datasource: new VersionDataSource(this.hydraApi),
    });
  };

  onSave = () => {
    if (this.MarketingBlurb.id) {
      this.hydraApi.updateMarketingBlurb(this.MarketingBlurb, this.MarketingBlurbSaved, this.MarketingBlurbSaveFailed);
    } else {
      this.hydraApi.createMarketingBlurb(this.MarketingBlurb, this.MarketingBlurbSaved, this.MarketingBlurbSaveFailed);
    }

    this.spinner.showSavingSpinner();
  };

  MarketingBlurbSaved = (result: ApiResult) => {
    if (result.success) {
      this.toastr.success(this.translate.instant('admin-marketing-blurb.saved'));
      this.returnToGrid();
    } else {
      this.errorsService.showErrorsModal(result.errors, 'admin-marketing-blurb.validation-errors');
    }
    this.spinner.hide();
  };

  MarketingBlurbSaveFailed = (error: any) => {
    this.toastr.error(this.translate.instant('admin-marketing-blurb.save-failed'));
    Log.error('admin-marketing-blurb.save-failed', error);
    this.spinner.hide();
  };

  onCancel = () => {
    this.returnToGrid();
  };

  returnToGrid = () => {
    this.router.navigateByUrl(
      'product/settings/BambaAdministratorApplication/BambaAdministratorApplication-Upland.Bamba.Application.Settings.MarketingBlurbs'
    );
  };

  MarketingBlurbLoaded = (ApiResult: any): void => {
    if (this.routeData.type === 'duplicate') {
      ApiResult.object.name = `${this.translate.instant('Admin.CopyOf')} ${ApiResult.object.name}`;
      ApiResult.object.id = '';
    }

    this.MarketingBlurb = new MarketingBlurb({ ...ApiResult.object });

    this.cdr.markForCheck();
    this.spinner.hide();
  };

  MarketingBlurbFailed = (error: any): void => {
    this.toastr.error(this.translate.instant('admin-marketing-blurb.load-failed'));
    Log.error('admin-marketing-blurb.load-failed', error);
    this.spinner.hide();
  };

  loadItems = (): void => {
    if (this.routeData.type === 'properties' || this.routeData.type === 'duplicate') {
      this.spinner.show(this.translate.instant('admin-marketing-blurb.loading-marketing-blurb'), document.body);
      this.hydraApi.getMarketingBlurb(this.routeParams.id, this.MarketingBlurbLoaded, this.MarketingBlurbFailed);
    }
  };

  activateTab = (view) => {
    this.activeTab = view;
  };

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}

class VersionDataSource extends FixedDataSource {
  constructor(private hydraApiService: HydraApiService) {
    super();
  }

  successCallBack: (items: IFieldOptions[]) => void;
  failureCallBack: (err: any) => void;

  getItems(
    startsWith: string,
    currentValue: string,
    successCallBack: (items: IFieldOptions[]) => void,
    failureCallBack: (err: any) => void
  ): void {
    this.successCallBack = successCallBack;
    this.failureCallBack = failureCallBack;

    this.hydraApiService.getMarketingBlurbsJiraVersions(this.versionsLoaded, this.failed);
  }

  versionsLoaded = (result: ApiResult): void => {
    if (!result.success) return;

    const options = new Array<IFieldOptions>();
    result.object.forEach((version: string) => {
      options.push({ name: version, value: version });
    });

    this.successCallBack(options);
  };

  failed = (err): void => {
    this.failureCallBack(err);
  };
}
