import { Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, MetaDataType } from 'shared-code-lib';
import { Blurb } from '../../../../blocks/models/marketing-blurb';

@Component({
  selector: 'app-blurb',
  templateUrl: './blurb.component.html',
  styleUrls: ['./blurb.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class BlurbComponent implements OnInit {
  @Input() blurb: Blurb;
  @Input() index: number;
  @Input() hideRemove;
  @Output() onRemoveBlurb = new EventEmitter<number>();

  summaryField: FieldDefinition;
  detailsField: FieldDefinition;
  learnMoreLinkField: FieldDefinition;

  constructor(private translate: TranslateService) {}

  ngOnInit(): void {
    this.createFields();
  }

  createFields() {
    this.summaryField = new FieldDefinition({
      type: MetaDataType.Text,
      label: 'blurb-summary',
      hideLabel: true,
      placeholder: this.translate.instant('admin-marketing-blurb.blurb-summary-placeholder'),
      required: true,
    });

    this.detailsField = new FieldDefinition({
      type: MetaDataType.LargeText,
      label: 'blurb-details',
      hideLabel: true,
      placeholder: this.translate.instant('admin-marketing-blurb.blurb-details-placeholder'),
      required: true,
    });

    this.learnMoreLinkField = new FieldDefinition({
      type: MetaDataType.Text,
      label: 'blurb-learn-more-link',
      hideLabel: true,
      placeholder: this.translate.instant('admin-marketing-blurb.blurb-learn-more-link-placeholder'),
      required: true,
    });
  }

  deleteBlurb() {
    this.onRemoveBlurb.emit(this.index);
  }
}
