export enum BrandType {
  UIC,
  IFX,
}

export enum State {
  New,
  Approved,
}

export class BrandLink {
  brandType: BrandType = BrandType.UIC;
  brandId: string;
  dealerId: string;
}

export class BrandInfo extends BrandLink {
  name: string;
  image: string;

  constructor(initializer: any = null) {
    super();
    if (initializer) {
      this.brandId = initializer.brandId;
      this.dealerId = initializer.dealerId;
      this.brandType = initializer.brandType;
      this.name = initializer.name;
      this.image = initializer.image;
    }
  }
}

export class Blurb {
  summary: string;
  details: string;
  learnMoreLink: string;

  constructor(init: any = null) {
    this.details = init ? init.details : '';
    this.summary = init ? init.summary : '';
    this.learnMoreLink = init ? init.learnMoreLink : '';
  }
}

export interface IMarketingBlurb {
  id: string;
  name: string;
  state: State;
  release: string;
  brandLinks: BrandLink[];
  blurbs: Blurb[];
  dateCreatedUTC: Date;
  dateModifiedUTC: Date;
}

export class MarketingBlurb implements IMarketingBlurb {
  id: string;
  name: string;
  state: State;
  release: string;
  brandLinks: BrandLink[] = [];
  blurbs: Blurb[] = [new Blurb()];
  dateCreatedUTC: Date;
  dateModifiedUTC: Date;

  public constructor(mb: IMarketingBlurb = null) {
    if (mb) {
      this.id = mb.id;
      this.name = mb.name;
      this.state = mb.state;
      this.release = mb.release;
      this.brandLinks = mb.brandLinks ? mb.brandLinks : [];
      this.blurbs = mb.blurbs ? mb.blurbs : [new Blurb()];
      this.dateCreatedUTC = mb.dateCreatedUTC;
      this.dateModifiedUTC = mb.dateModifiedUTC;
    }
  }

  public serializable(): IMarketingBlurb {
    return {
      id: this.id,
      name: this.name,
      state: this.state,
      release: this.release,
      brandLinks: this.brandLinks,
      blurbs: this.blurbs,
      dateCreatedUTC: this.dateCreatedUTC,
      dateModifiedUTC: this.dateModifiedUTC,
    };
  }
}

export class UserBlurbInfo {
  id: string;
  blurbs: Array<Blurb>;
  seen: boolean;
}
