import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdminMarketingBlurbComponent } from './admin-marketing-blurb.component';

const routes: Routes = [
  {
    path: 'duplicate/:id',
    component: AdminMarketingBlurbComponent,
    data: { type: 'duplicate' },
    pathMatch: 'full',
  },
  {
    path: 'create',
    component: AdminMarketingBlurbComponent,
    data: { type: 'create' },
    pathMatch: 'full',
  },
  {
    path: ':id',
    component: AdminMarketingBlurbComponent,
    data: { type: 'properties' },
    pathMatch: 'full',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AdminMarketingBlurbRoutingModule {}
