import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';

import { TranslateService } from '@ngx-translate/core';
import { ISpinnerService } from 'shared-code-lib';
import { BrandInfo, BrandType, MarketingBlurb } from '../../../blocks/models/marketing-blurb';
import { HydraApiService } from '../../../blocks/services/hydra-api.service';
import { ApiResult } from '../../../blocks/services/models';

@Component({
  selector: 'app-brand-list',
  templateUrl: './brand-list.component.html',
  styleUrls: ['./brand-list.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class BrandListComponent implements OnInit {
  @Input() marketingBlurb: MarketingBlurb;
  @Input() spinner: ISpinnerService;

  show = false;
  available: Array<BrandInfo> = [];
  current: Array<BrandInfo> = [];
  constructor(private translate: TranslateService, private hydraApi: HydraApiService) {}

  ngOnInit() {
    this.loadBrands();
  }

  loadBrands() {
    this.spinner.show(this.translate.instant('admin-marketing-blurb.loading-brands'), document.body);
    this.hydraApi.getBrands(this.loaded, this.failed);
  }

  failed = (err): void => {
    console.log('failed', err);
    this.spinner.hide();
  };

  currentChanged = (items: Array<BrandInfo>) => {
    this.marketingBlurb.brandLinks = [];
    for (let item of items) {
      this.marketingBlurb.brandLinks.push(item);
    }
  };

  loaded = (apiResult: ApiResult) => {
    if (apiResult.success) {
      this.available = (apiResult.object as Array<any>).map((item) => {
        const brandInfo = new BrandInfo(item);
        brandInfo.image = brandInfo.brandType == BrandType.UIC ? 'assets/img/uic.svg' : 'assets/img/ifx.svg';
        return brandInfo;
      });

      this.current = this.available.filter((r) =>
        this.marketingBlurb.brandLinks.some(
          (ri) => ri.brandId == r.brandId && ri.brandType == r.brandType && ri.dealerId == r.dealerId
        )
      );

      this.show = true;
      this.spinner.hide();
    } else {
      this.failed(apiResult.errors);
    }
  };
}
