import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { Component, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import { DelayService } from 'shared-code-lib';
import { Blurb, MarketingBlurb } from '../../../blocks/models/marketing-blurb';

@Component({
  selector: 'app-blurbs',
  templateUrl: './blurbs.component.html',
  styleUrls: ['./blurbs.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class BlurbsComponent implements OnInit {
  @ViewChild(PerfectScrollbarDirective, { static: false }) directiveRef?: PerfectScrollbarDirective;
  @Input() marketingBlurb: MarketingBlurb;

  errors: string[] = [];
  constructor(private delay: DelayService) {}

  ngOnInit(): void {
    this.errors = [];
  }

  addNewblurb() {
    this.marketingBlurb.blurbs.push(new Blurb());

    this.delay.execute(() => this.directiveRef.scrollToBottom(0, 100), 10);
  }

  removeBlurb(index: number) {
    if (this.marketingBlurb.blurbs.length <= 1) return;
    this.marketingBlurb.blurbs.splice(index, 1);
    this.directiveRef.update();
  }

  moveItem(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.marketingBlurb.blurbs, event.previousIndex, event.currentIndex);
  }
}
